body {
  margin: 20px;
  font-family: Arial, sans-serif;
}

h1, h2 {
  color: #333;
}

input {
  border-radius: 20px;
  width: 250px;
  height: 30px;
}

.getMoviesBtn {
  border: none;
  border-radius: 10px;
  width: 200px;
  height: 40px;
}

.getMoviesBtn:hover {
  color: #fff;
  background-color: #000;
  transition: all .3s;
  box-shadow: 0 0 10px 1px #606069;
}

.addmoviesBtn {
  border: none;
  border-radius: 10px;
  margin: 5px;
  padding: 10px 20px;
}

.addmoviesBtn:hover {
  color: #fff;
  background-color: #000;
  transition: all .3s;
  box-shadow: 0 0 10px 1px #606069;
}

label {
  margin-right: 10px;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin-top: 20px;
}

form {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

th, td {
  text-align: left;
  border-bottom: 1px solid #ccc;
  padding: 10px;
}

th {
  background-color: #f0f0f0;
}

button[data-action="update"] {
  color: #fff;
  background-color: #1e90ff;
  border: none;
}

button[data-action="edit"] {
  color: #fff;
  background-color: #4caf50;
  border: none;
}

button[data-action="delete"] {
  color: #fff;
  background-color: #f44336;
  border: none;
}

.modal {
  z-index: 1;
  background-color: rgba(0, 0, 0, .4);
  width: 100%;
  height: 100%;
  padding-top: 60px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  background-color: #fefefe;
  border: 1px solid #888;
  flex-direction: column;
  gap: 20px;
  width: 80%;
  margin: 5% auto;
  padding: 20px;
  display: flex;
}

.saveChanges {
  margin: 0 auto;
  display: block;
}

